// 获取登录接口
export const useSsoLogin = (param?: Record<string, string | number>) => {
  const dataParams = {
    ...param,
  };
  const browserMatch: any = uaMatch(userAgent.toLowerCase());
  _.set(dataParams, "deviceType", "PC");
  _.set(dataParams, "browserType", browserMatch.browser);
  _.set(dataParams, "browserVersion", browserMatch.version);
  return useQuery(
    ["loginServer", ],
    () => http.post(`/${"login"}`, dataParams),
    {
      enabled: !!param?.loginName && !!param?.pwd,
      // 这个开启定时的
      // refetchInterval:2*1000
    }
  );
};
