import React, { ReactElement, useState } from "react";
import { RegisterScreen } from "./registerScreen/register";
import { Button } from "antd";
import LoginScreen from "./loginScreen";

 const UserAppScreen = ():ReactElement => {
  const [loginType, setLoginType] = useState<boolean>(false);
  const handleOnclickSelectLogin = () => {
    setLoginType(!loginType);
  };
  return (
    <>
      {loginType ? (
        <RegisterScreen></RegisterScreen>
      ) : (
        <LoginScreen></LoginScreen>
      )}
      {/* <Button type={"primary"} onClick={handleOnclickSelectLogin}>点击注册</Button> */}
    </>
  );
};
export default UserAppScreen