import React, { useEffect,  useState } from 'react';
import { Form, Input, Button, message, Space } from 'antd';
import { useNavigate } from 'react-router-dom';
import { Container, FormContainer, LoginBg, LoginTemplate } from './style';
import { useSsoLogin } from 'api/modules/login';
import _ from 'lodash';
import { useDocumentTitle } from 'utils/hooks/authUtils';
import MoonSunSwitch from 'style/componends/Switch/MoonSunSwitch';
import WaveText from 'style/componends/Text/WaveText';
import Quality from '../componends/Quality';

interface AuthLoginForm {
  loginName: string;
  pwd: string | number;
}
const LoginScreen = () => {
  useDocumentTitle('LoginUserInfo', false);
  const navigate = useNavigate();
  const [form] = Form.useForm<{ loginName: string; pwd: number }>();
  const [loginParams, setLoginParams] = useState<Partial<AuthLoginForm>>({});
  const { data: loginData } = useSsoLogin(loginParams);
  const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };
  const tailLayout = {
    wrapperCol: { offset: 10, span: 16 },
  };

  //这里调了NGES的接口
  const onFinish = (values: AuthLoginForm) => {
    setLoginParams(values);
  };
  useEffect(() => {
    try {
      if (!_.isEmpty(loginData)) {
        if (loginData?.head.code === 401 || loginData?.head.code === 500) {
          message.error(`${loginData.head.msg}`);
        } else {
          console.log(loginData);
          _.forEach(
            _.pick({ ...loginData?.body.profile }, [
              'name',
            ]),
            (val: string, key: string) => {
              localStorage.setItem(key, val);
            }
          );
          message.success('登录成功');
          // 默认跳转到首页
          // 保留登录成功的状态    userId   token   role  isLogin
          localStorage.setItem('userId', loginData?.body.userId);
          localStorage.setItem('token', loginData?.head.token);
          localStorage.setItem('isLogin', JSON.stringify(true));
          navigate('/home');
          // window.location.reload();
        }
      }
    } catch (error) {
      console.log(error);
    }
  }, [loginData, navigate]);
  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };
  const onReset = () => {
    form.resetFields();
  };
  return (
    <>
      <Quality />
      <Container>
        <LoginTemplate>
          <MoonSunSwitch />
          <LoginBg />
          <FormContainer>
            <WaveText />
            <Form
              name="normal_login"
              initialValues={{
                remember: true,
              }}
              {...layout}
              form={form}
              onFinish={onFinish}
              onFinishFailed={onFinishFailed}
            >
              <Form.Item
                name="loginName"
                label="Username"
                rules={[
                  {
                    required: true,
                    message: '请输入用户名',
                  },
                ]}
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="Password"
                name="pwd"
                rules={[
                  {
                    required: true,
                    message: '请输入密码!',
                  },
                ]}
              >
                <Input.Password />
              </Form.Item>
              <Form.Item {...tailLayout}>
                <Space align="center" size="large">
                  <Button type="primary" htmlType="submit">
                    登录
                  </Button>
                  <Button onClick={onReset} type="primary" danger ghost>
                    重置
                  </Button>
                </Space>
              </Form.Item>
            </Form>
          </FormContainer>
        </LoginTemplate>
      </Container>
    </>
  );
};

export default LoginScreen;
